.CodeMirror {
  padding: 10px;
  margin: 10px 0;
  height: auto !important;
}
.CodeMirror pre {
  padding: 0 20px;
}
.code-view-wrapper {
  position: relative;
  margin-bottom: 18px;
  border: 1px dashed #f1f1f1;
  padding: 18px 18px 28px 18px;
}
.code-view-wrapper .code-view-toolbar {
  right: 0;
  bottom: 0;
  border-bottom: 1px dashed #f5f5f5;
  text-align: right;
}
.code-view-wrapper .code-view-toolbar .btn {
  border-style: dashed;
}
.code-view-wrapper .code-view-toolbar .btn .icon-code {
  font-size: 20px;
}
.code-view-wrapper .doc-code {
  height: 0;
  overflow: hidden;
}
.code-view-wrapper .doc-code.show {
  height: auto;
}
.code-view-wrapper .code-view {
  padding: 10px 0;
  position: relative;
}
.code-view-wrapper .code-view:after {
  position: absolute;
  top: 18px;
  left: 18px;
  font-size: 12px;
  font-weight: 300;
  color: #959595;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.code-view-wrapper .code-view-error {
  color: red;
}
.rs-icon {
  display: inline-flex;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.rs-icon[tabindex] {
  cursor: pointer;
}
.rs-icon-spin {
  animation: icon-spin 2s infinite linear;
}
.rs-icon-pulse {
  animation: icon-spin 1s infinite steps(8);
}
.rs-icon-flip-horizontal {
  transform: scaleX(-1);
}
.rs-icon-flip-vertical {
  transform: scaleY(-1);
}
@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
.btn-toolbar {
  padding: 10px 0;
}
